export class Month {
    constructor(owner, value) {
        this.daysName = [];
        this.range = owner;
        this.value = value;
        const firstDay = {
            year: this.value.year,
            month: this.value.month,
            day: 1,
        };
        const year = { year: this.value.year };
        this.monthName = this.range.dateUtil.getMonthName(this.value, this.range.options.culture, this.range.options.lid);
        this.dayInMonth = this.range.dateUtil.getDaysNumber(this.value, this.range.options.culture);
        this.firstDayInMonth = this.range.dateUtil.getWeekday(firstDay, this.range.options.culture);
        this.currentDate = this.range.dateUtil.getCurrentDate();
        if (this.firstDayInMonth == 7) {
            this.firstDayInMonth = 0;
        }
        let lastDay = {
            year: this.value.year,
            month: this.value.month,
            day: this.dayInMonth,
        };
        this.lastDayInMonth = this.range.dateUtil.getWeekday(lastDay, this.range.options.culture);
        if (this.lastDayInMonth == 7) {
            this.lastDayInMonth = 0;
        }
        this.isLeapYear = this.range.dateUtil.getIsLeapYear(year, this.range.options.culture);
        this.days = new Array();
        this.daysHtmlElement = new Array();
        for (let i = 0; i < this.dayInMonth; i++) {
            let currentDay = new Day(this, (i + 1));
            this.days.push(currentDay);
            this.daysHtmlElement.push(currentDay.generateDaysUi());
        }
        this.daysName = this.range.dateUtil.getDayNames(this.range.options.lid);
    }
}
