class PersianDateUtil {
    constructor() {
        this.lidConvertor = {
            1: "fa",
            2: "en",
        };
        this.persianDate = require("persian-date");
    }
    getMonthName(month, culture, lid) {
        return new this.persianDate([month.year, month.month, 1])
            .toLocale(this.lidConvertor[lid])
            .format("MMMM");
    }
    getDaysNumber(month, culture) {
        return new this.persianDate([month.year, month.month, 1])
            .toLocale(culture)
            .daysInMonth();
    }
    getDayName(day, culture, lid) {
        return new this.persianDate([day.year, day.month, day.day])
            .toLocale(culture)
            .format("dddd");
    }
    getBasisDayId(day) {
        return 0;
    }
    getWeekday(day, culture) {
        return (new this.persianDate([day.year, day.month, day.day])
            .toLocale(culture)
            .day() - 1);
    }
    getIsHoliday(day, culture) {
        let weekday = new this.persianDate([day.year, day.month, day.day])
            .toLocale(culture)
            .day() - 1;
        if (weekday == 6) {
            return true;
        }
        else {
            return false;
        }
    }
    getIsLeapYear(year, Culture) {
        return new this.persianDate([year.year, 12, 1])
            .toLocale(Culture)
            .isLeapYear();
    }
    getMonthValueList(from, to) {
        let months = [];
        let currentYear = from.year;
        for (var i = from.month; i <= to.month; i++) {
            months.push({ year: from.year, month: i });
            if (i == 12) {
                currentYear = currentYear + 1;
            }
        }
        return months;
    }
    getCurrentDate() {
        var now = new this.persianDate();
        var basisCoreObjToday = {
            year: now.year(),
            month: now.month(),
            day: now.date(),
        };
        return basisCoreObjToday;
    }
    getDayNames(lid) {
        let weekName = lid == 1
            ? [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ]
            : ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
        return weekName;
    }
    nextMonth(month, culture) {
        return null;
    }
    prevMonth(month, culture) {
        return null;
    }
    getTodayText(lid) {
        return lid == 1 ? "" : "Today";
    }
}
class BasisCoreDateUtil {
    constructor() {
        this.sMonthsNameFa = [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
        ];
        this.sMonthsNameEn = [
            "",
            "Farvardin",
            "Ordibehesht",
            "Khordad",
            "Tir",
            "Mordad",
            "Shahrivar",
            "Mehr",
            "Aban",
            "Azar",
            "Dey",
            "Bahman",
            "Esfand",
        ];
        this.numberDayOfMonth = [
            0, 31, 31, 31, 31, 31, 31, 30, 30, 30, 30, 30, 29,
        ];
        this.weeksName = [
            "",
            "",
            "",
            "",
            "",
            "",
            "",
            "",
        ];
        this.weeksEnName = [
            "",
            "Sun",
            "Mon",
            "Tue",
            "Wed",
            "Thu",
            "Fri",
            "Sat",
        ];
        this.json = require("./tdate.json");
    }
    getObj(year, month, day) {
        if (year > 1300 && year < 1500) {
            return this.json.find((item) => {
                return item.syear == year && item.smonth == month && item.sdate == day;
            });
        }
        else {
            return this.json.find((item) => {
                return item.nyear == year && item.mmonth == month && item.mdate == day;
            });
        }
    }
    getMonthName(month, culture, lid) {
        let sMonth = this.getObj(month.year, month.month, 1).smonth;
        if (lid == 1) {
            return this.sMonthsNameFa[sMonth];
        }
        else if (lid == 2) {
            return this.sMonthsNameEn[sMonth];
        }
    }
    getDaysNumber(month, culture) {
        let sMonth = this.getObj(month.year, month.month, 1).smonth;
        return this.numberDayOfMonth[sMonth];
    }
    getDayName(day, culture, lid) {
        let weekday = this.getObj(day.year, day.month, day.day).weekday;
        if (lid == 1) {
            return this.weeksName[weekday];
        }
        else {
            return this.weeksEnName[weekday];
        }
    }
    getBasisDayId(day) {
        return this.getObj(day.year, day.month, day.day).id;
    }
    getWeekday(day, culture) {
        return this.getObj(day.year, day.month, day.day).weekday;
    }
    getIsHoliday(day, culture) {
        let weekday = this.getObj(day.year, day.month, day.day).weekday;
        if (weekday == 6) {
            return true;
        }
        else {
            return false;
        }
    }
    getIsLeapYear(year, Culture) {
        return false;
    }
    getMonthValueList(from, to) {
        let months = [];
        let currentYear = from.year;
        for (var i = from.month; i <= to.month; i++) {
            months.push({ year: from.year, month: i });
            if (i == 12) {
                currentYear = currentYear + 1;
            }
        }
        return months;
    }
    getCurrentDate() {
        var date = new Date();
        var today = this.getObj(date.getFullYear(), (date.getMonth() + 1), date.getDate());
        var basisCoreObjToday = {
            year: today.syear,
            month: today.smonth,
            day: today.sdate,
        };
        return basisCoreObjToday;
    }
    getDayNames(lid) {
        let weekName = lid == 1
            ? [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ]
            : ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
        return weekName;
    }
    nextMonth(month, culture) {
        let retVal;
        let nextMonth = (month.value.month + 1);
        let currentYear = month.value.year;
        if (nextMonth > 12) {
            currentYear++;
            nextMonth = 1;
        }
        retVal = { year: currentYear, month: nextMonth };
        return retVal;
    }
    prevMonth(month, culture) {
        let retval;
        let prevMonth = (month.value.month - 1);
        let currentYear = month.value.year;
        if (prevMonth < 1) {
            currentYear--;
            prevMonth = 12;
        }
        retval = { year: currentYear, month: prevMonth };
        return retval;
    }
    getTodayText(lid) {
        return lid == 1 ? "" : "Today";
    }
}
